<template>
  <v-container>
    <v-layout
      column
      align-center
      id="wrap"
    >
      <v-flex xs6>
        <div id="lnb">
          <ul>
            <li><a href="#">lnb01</a></li>
            <li><a href="#">lnb02</a></li>
            <li><a href="#">lnb03</a></li>
            <li><a href="#">lnb04</a></li>
            <li><a href="#">lnb05</a></li>
            <li><a href="#">lnb06</a></li>
          </ul>
        </div>
      </v-flex>
      <v-flex>
        <div id="rec" @click="paused()">
          rec
        </div>
      </v-flex>
      <v-flex>
        <div id="rec1" @click="start()">
          rec1
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import $ from 'jquery';
export default {
  data() {
    return {
    };
  },
  created(){
  },
  computed: {
  },
  methods: {
    paused(){
      $('#rec').toggleClass('rec_stop');
    },
    start(){
      // $(this).addClass("class_name");    선택된 객체에서 class를 추가한다.
      // $(this).removeClass("class_name"); 선택된 객체에서 class를 삭제한다.
      // $(this).toggleClass("class_name"); 토글 형식으로 class를 바꾸어준다.
      $('#rec1').toggleClass('rec1_start');
    }
  },
  watch:{
  }
}
</script>

<style>
li {
  list-style: none;
}
#wrap #lnb{
  width: 250px;
  background-color: #ccc;
}
#wrap #lnb ul li a{
  width: 100px;
  height: 50px;
  line-height: 25px;
  border-radius: 10px;
  margin-top: 2px;
  padding:10px 25px;
  background-color: #ffd800;
  display: block;
  color:#fff;
  font-weight:bold;
}
#wrap #lnb ul li a:hover{
  animation-name: ani;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
@keyframes ani{
  from{
    width: 100px;
  }
  to{
    width:200px;
    background-color: #a08a10;
  }
}

#wrap #rec {
  position: absolute;
  width: 100px;
  height: 100px;
  line-height: 100px;
  border-radius: 10px;
  text-align: center;
  left:0;
  background-color: #ffff00;
  animation-name: rec_ani;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.rec_stop{
  animation-play-state: paused;
}
/* #wrap #rec:hover{
  animation-play-state: paused;
} */



@keyframes rec_ani{
  0%{
    width: 100px;
    left: 0;
    transform: rotate(0deg);
  }
  100%{
    width:200px;
    height:200px;
    border-radius:100px;
    line-height: 200px;
    background-color: #a09a10;
    left:500px;
    transform: rotate(360deg);
    font-size: 4rem;
  }
}
#wrap #rec1 {
  position: absolute;
  width: 100px;
  height: 100px;
  line-height: 100px;
  border-radius: 10px;
  text-align: center;
  left:0;
  background-color: #ffff00;  
}
.rec1_start {
  animation-name: rec_rotate_ani;
  animation-duration: 2s;
  animation-iteration-count: 3;
  animation-direction:  alternate;
  animation-fill-mode:  normal;
  /* transform-origin: top right;  */
  transform-origin: 100% 30%;
  /* transform의 중심점 설정 : 가로% 세로%;  0 0 객체의 상단 0,0위치*/
}

/* #wrap #rec1:hover{
  animation-play-state: paused;
} */

@keyframes rec_rotate_ani{
  0%{
    transform: rotate(0deg);
  }
  100%{
    background-color: #a09a10;
    transform: rotate(360deg);
  }
}

</style>
